
.box{
   width:100%;
   height: 100%;
   background: url("../../../static/images/timg.jpg") no-repeat ;
   background-size: 100% 100%;
   position: fixed;
   top:0!important;
   left: 0;
   top:0;
   bottom: 0;
}
.login{
   z-index: 999;
   width:5rem;
   height:4rem;
   position: fixed;
   top:50%;
   left: 50%;
   margin-left: -2.5rem;
   margin-top: -2rem;
   background: rgba(238,238,230,.38);
   box-shadow: 3px 3px 3px rgba(6,6,6,.5);
}
.input-box{
   height: .6rem;
   line-height: .6rem;
   clear: both;
   width:4rem;
   margin: .3rem auto;
   border-bottom: 1px solid #eee;
}
.input-box p{
   margin-right: .2rem;
   color: #fff;
   -webkit-text-shadow: 3px 3px 3px rgba(6,6,6,.5);
   -moz-text-shadow: 3px 3px 3px rgba(6,6,6,.5);
   text-shadow: 3px 3px 3px rgba(6,6,6,.5);
}
.input-box input{
   border: 0;
   background: transparent;
   -webkit-text-shadow: 3px 3px 3px rgba(6,6,6,.5);
   -moz-text-shadow: 3px 3px 3px rgba(6,6,6,.5);
   text-shadow: 3px 3px 3px rgba(6,6,6,.5);
   color: #fff;
}
::-webkit-input-placeholder{
   color: #fff;
}
::-moz-placeholder{
   color: #fff;
}
.head{
  color: rgba(252,164,10,1);
  font-size: .3rem;
  text-align: center;
  -webkit-text-shadow: 3px 3px 3px rgba(6,6,6,.5);
  -moz-text-shadow: 3px 3px 3px rgba(6,6,6,.5);
  text-shadow: 3px 3px 3px rgba(6,6,6,.5);
}
.btn{
   width:3rem;
   height:.6rem;
   line-height: .6rem;
   color: #fff;
   padding: 0;
   background: rgba(186,144,104,.5);
   box-shadow: 3px 3px 3px rgba(6,6,6,.5);
}
.btn:hover{
   color: #fff;
}



